          .row.justify-content-center.mb-5
            .col-lg-1.col-2.mb-lg-0.mb-3
              img(src='@/assets/curso/tema2/24.svg', alt='') 
                
            .col-lg-11.col-20
              p #[span(style='color: #e95800').fw-bold Gay:] hombre que se siente atraído sexual y afectivamente por otros hombres.  
          .row.justify-content-center.mb-5
            .col-lg-1.col-2.mb-lg-0.mb-3
              img(src='@/assets/curso/tema2/25.svg', alt='') 
            
            .col-lg-11.col-20
              p #[span(style='color: #ffb000').fw-bold Bisexual:] persona que siente atraída sexual y afectivamente hacia hombres y mujeres indistintamente. 
              
          .row.justify-content-center.mb-5
            .col-lg-1.col-2.mb-lg-0.mb-3
              img(src='@/assets/curso/tema2/26.svg', alt='') 
                
            .col-lg-11.col-20
              p #[span(style='color: #3d9924').fw-bold Trans:] es la identidad de género que no corresponde con aquellas asignadas al nacer. Dentro de la clasificación trans se encuentra la denominación de transgénero, quien es la persona que nace con ciertas características físicas que no tienen correspondencia con el género con el cual se identifica. La persona transexual es quien ha comenzado un tratamiento hormonal o de nivel quirúrgico para realizar cambio de sexo. 
          .row.justify-content-center.mb-5
            .col-lg-1.col-2.mb-lg-0.mb-3
              img(src='@/assets/curso/tema2/27.svg', alt='') 
                
            .col-lg-11.col-20
              p #[span(style='color: #009ee0').fw-bold Intersexual:] persona que nace con características físicas de ambos géneros.  
          .row.justify-content-center.mb-5
            .col-lg-1.col-2.mb-lg-0.mb-3
              img(src='@/assets/curso/tema2/28.svg', alt='') 
                
            .col-lg-11.col-20
              p #[span(style='color: #853c89').fw-bold Queer' o sin etiquetas:]  personas que no se categorizan con las etiquetas tradicionales o aceptadas socialmente.  
          .row.justify-content-center.mb-5
            .col-lg-1.col-2.mb-lg-0.mb-3
              img(src='@/assets/curso/tema2/29.svg', alt='') 
                
            .col-lg-11.col-20
              p #[span(style='color: #12263f').fw-bold Otras:] personas que no se sienten identificadas ni como lesbiana, gay, trans, bisexual o intersexual.
    .titulo-tres.mb-5
      h5.mb-0 Dentro del acrónimo tenemos identidades y orientaciones  
