              p Persona que siente atraída sexual y afectivamente hacia hombres y mujeres indistintamente. 
        .col-lg-3.mb-lg-0.mb-3
          .tarjeta.overflow-hidden.bg-white.shadow.h-100
            img(src='@/assets/curso/tema2/34b.png', alt='')
            .p-4.text-center
              h5 La Intersexualidad
              p  Se trata de una variación orgánica bajo la cual el desarrollo del sexo cromosómico, gonadal o anatómico no coincide con los dos sexos que tradicionalmente se asignan. Se trata de una condición biológica y, en algunos casos, política, debido a que algunas personas construyen su identidad a partir de la no identificación con los dos sexos -masculino y femenino- que cultural y socialmente se establecen.
      
      
      .tarjeta(style="background: #423755").p-3.px-4.mb-5
        .row.align-items-center
          .col-auto
            img(src='@/assets/curso/tema2/58.svg', alt='', style="max-width: 64px").mx-auto
          .col
            p.text-white.mb-0 Es importante respetar siempre los pronombres de las personas. Si no sabes bien qué palabras utilizar para describirlas, pregúntales con respeto.

